@import url(https://fonts.googleapis.com/css2?family=Manrope&family=Alice&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  text-align: center;
}

body {
  background-color: rgb(238, 231, 217);
  background-image: url(https://images.unsplash.com/photo-1496412705862-e0088f16f791?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80);
  background-size: 100%;
  background-attachment: fixed;
}

.Home {
  width: 80%;
  margin: 0 auto;
}

.tag {
  color: white;
  background-color: teal;
  display: inline-block;
  border-radius: 15px;
  margin: 10px;
  padding: 0 10px 0 10px;
}

.recipe-body {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}

.title-font {
  font-family: "Alice", serif;
}

.food-main {
  border-radius: 15px;
  background-color: rgb(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  font-family: "Manrope", sans-serif;
}

.food-image {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.food-ingredient {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 10px;
  justify-content: center;
  align-items: center;
}

